import React, { useState } from "react"
import PropTypes from "prop-types"
import Button from "../components/button"
import data from "../data.json"

import {
  Box,
  Flex,
  Label,
  Input,
  Select,
  Textarea,
  Radio,
  Checkbox,
  Slider,
  Text,
} from "theme-ui"

const Form = () => {
  const [form, setForm] = useState({})
  const [voivodeship, setVoivodeship] = useState(null)
  const handlePrint = () => {
    window.localStorage.setItem("form", JSON.stringify(form))
    window.open("/pdf", "_blank")
  }
  const handleSetForm = (key, value) => setForm({ ...form, [key]: value })

  const handleSelectVoivodeship = ({ target: { value } }) => {
    setVoivodeship(data.voivodeships[value])
  }

  const handleSelectCounty = ({ target: { value } }) => {
    handleSetForm("county", voivodeship.counties[value])
  }

  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        padding: "2rem 1rem",
      }}
    >
      <Text>Aby dostac pdf, wypelnij ponizszy formularz</Text>
      <Box as="form" onSubmit={e => e.preventDefault()}>
        <Label htmlFor="name">Imie</Label>
        <Input
          name="name"
          id="name"
          mb={3}
          onChange={({ target: { value } }) => handleSetForm("name", value)}
        />
        <Label htmlFor="surname">Nazwisko</Label>
        <Input
          name="surname"
          id="surname"
          mb={3}
          onChange={({ target: { value } }) => handleSetForm("surname", value)}
        />
        <Label htmlFor="sound">Wojewodztwo</Label>
        <Select
          name="sound"
          id="sound"
          mb={3}
          onChange={handleSelectVoivodeship}
        >
          <option></option>
          {data.voivodeships.map((v, idx) => (
            <option key={v.name} value={idx}>
              {v.name}
            </option>
          ))}
        </Select>
        {voivodeship && (
          <>
            <Label htmlFor="county">Powiat</Label>
            <Select
              name="county"
              id="county"
              mb={3}
              onChange={handleSelectCounty}
            >
              <option></option>
              {voivodeship.counties.map((c, idx) => (
                <option key={c.name} value={idx}>
                  {c.name}
                </option>
              ))}
            </Select>
          </>
        )}
        <Label htmlFor="comment">Comment</Label>
        <Textarea name="comment" id="comment" rows="6" mb={3} />
        <Button onClick={handlePrint} variant="secondary">
          Drukuj
        </Button>
      </Box>
    </div>
  )
}

export default Form
